import React from 'react';
import {useParams} from "react-router-dom";
import {useSelector} from "react-redux";
import useTranslation from "../../../hooks/useTranslation";

const BasicElement = () => {

    // manage global state
    const {fetched} = useSelector(state => state.fetchTaxonomiesReducer);
    const data = {
        slug: fetched[0].slug,
        singular: fetched[0].singular,
        plural: fetched[0].plural,
    };

    // manage local state
    const {taxonomy} = useParams();

    return (
        <div>
            <table className={`acpt-table acpt-table-secondary ${document.globals.is_rtl === true ? `rtl` : ``}`}>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Slug")}
                    </th>
                    <td>{data.slug}</td>
                </tr>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Singular")}
                    </th>
                    <td>{data.singular}</td>
                </tr>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Plural")}
                    </th>
                    <td>{data.plural}</td>
                </tr>
            </table>
        </div>
    );
};

export default BasicElement;